  .dropdown {
    position: relative;
    display: inline-block;
    
  }
  
  .dropdown-button {
    background: rgb(1, 29, 84); /* Use your desired color */
    color: white;
    padding: 10px;
    border: none;
    cursor: pointer;
    border-radius: 4px;
    display: flex;
    align-items: center;
    gap: 8px;
    border-radius: 10px;
  }
  
  .dropdown-button img {
    border-radius: 50%;
  }
  
  .dropdown-content {
    display: none;
    position: absolute;
    left: -40px;
    background-color: rgb(1, 29, 84);
    min-width: 160px;
    color: white;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
  }
  
  .dropdown-content a {
    color: white;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
  }
  
  .dropdown:hover .dropdown-content {
    display: block;
  }
  
  /* This will be applied when the dropdown is open */
  .dropdown .dropdown-content {
    display: block;
    background-color: black;
  }
  /* Add this to your CSS file */
  .transition-width {
    transition: width 300ms ease-in-out;
  }
  
  /* This will hide the sidebar content when the width is 0 */
  .w-0 {
    width: 0;
    overflow: hidden;
  }
  /* App.css or wherever your styles are defined */
  .bg-gray-700 {
    background-color: #4a5568; /* This is the Tailwind CSS gray-700 color */
  }
  
  /* Add this to your CSS file */
  @keyframes fadeInRight {
    from {
      opacity: 0;
      transform: translateX(20px);
    }
    to {
      opacity: 1;
      transform: translateX(0);
    }
  }
  
  .fade-in-right {
    animation: fadeInRight 1s ease forwards;
  }
